#modalJoinConfirm {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #000000, $alpha: 0.15);
  z-index: 5;
  > .container {
    width: 420px;
    height: 200px;
    background: #fff;
    position: absolute;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    left: 50%;
    border-radius: 15px;
    overflow: hidden;
    .gap {
      width: 100%;
      height: 100%;
      .content {
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        justify-content: center;
        h3 {
          width: 100%;
          text-align: center;
          position: absolute;
          top: 35%;
          transform: translateY(-50%);
          font-size: 16px;
          font-weight: 700;
          color: #222;
          letter-spacing: -0.07px;
        }
        button {
          width: 300px;
          height: 40px;
          position: absolute;
          top: 72.5%;
          transform: translateY(-50%);
          margin: 9px 0 0 0;
          height: 52px;
          font-size: 15px;
          border-radius: 12px;
          border: 0;
          background: #222;
          line-height: 52px;
          text-align: center;
          letter-spacing: -0.07px;
          color: #fff;
          font-weight: 700;
          cursor: pointer;

        }
      }
    }
  }
}
